export default {
	name: "uni-grid",
	props: {
		options: Array,
		type: { //布局格式，长方形oblong，正方形square
			type: String,
			default: 'square'
		},
		columnNum: { //每一行有多少个
			type: [Number, String],
			default: 3
		},
		showOutBorder: { //显示外边框
			type: [Boolean, String],
			default: true
		},
		showBorder: { //是否显示border，如果为false，showOutBorder无效
			type: [Boolean, String],
			default: true
		}
	},
	data() {
		return {}
	},
	created() {
		this.columnNumber = this.gridGroup[0].length
	},
	computed: {
		gridGroup() {
			let group = []
			let groupItem = []
			this.options && this.options.forEach((item, index) => {
				groupItem.push(item)
				if (index % this.columnNum === this.columnNum - 1) {
					group.push(groupItem)
					groupItem = []
				}
			})
			if (groupItem.length > 0) {
				if (this.columnNum > groupItem.length) {
					for (let i = 0,length = groupItem.length; i < this.columnNum - length; i++) {
						groupItem.push({seize:true})
					}
				}
				group.push(groupItem)
			}
			groupItem = null
			return group
		}
	},
	methods: {
		onClick(index, num,url) {
			uni.navigateTo({
				url: url+'?num='+num.toString()+'&index='+index.toString()
			});
		}
	}
}
